import React from 'react';
import Swipeable from 'react-swipy';
import SwiperButtons from '../swiper-buttons';
import SwiperCard from '../swiper-card';

const Swiper = ({cards, onSwipe, onAfterSwipe, helper, updateLoader}) => {
	if(cards.length < 1) updateLoader();

	return (
		<div className='swiper'>
			{
				cards.length > 0 && (
					<div className='swiper__wrapper'>
						<Swipeable
							buttons={({left, right}) => <SwiperButtons left={left} right={right} card={cards[0]} helper={helper}/>}
							onSwipe={onSwipe}
							onAfterSwipe={onAfterSwipe}
						>
							<SwiperCard card={cards[0]}/>
						</Swipeable>
					</div>
				)
			}
		</div>
	);
};

export default Swiper;
