import React, {Component} from 'react';
import Counter from '../counter';
import Swiper from '../swiper';
import SwiperService from '../../service';
import WasPreloader from "../was-preloader";
import '../../helpers/resumeHandler';
import deckSound from '../../assets/sounds/deck-sound.wav';
import errorSound from '../../assets/sounds/error-sound.wav';
import successSound from '../../assets/sounds/success-sound.mp3';
import overTimeSound from '../../assets/sounds/over-time-sound.wav';
import {Howl, Howler} from 'howler';

export default class SwiperWrapper extends Component {
	state = {
		cards: [],
		timer: 60,
		points: 0,
		errorPoints: 0,
		loading: true,
		muted: false
	};

	componentDidMount() {
		const deckSing = new Howl({
			src: [deckSound]
		});


		const successSing = new Howl({
			src: [successSound]
		});

		const errorSing = new Howl({
			src: [errorSound]
		});

		const overTimeSing = new Howl({
			src: [overTimeSound]
		});

		SwiperService.getData()
			.then((data) => {
				const {timer, cards, helper} = data;
				this.setState({
					...this.state,
					timer,
					cards,
					helper,
					totalPoints: cards.length,
					successSing,
					errorSing,
					overTimeSing,
					loading: false
				});
				this.timerAction();
			})
			.catch(e => console.error(e));


		deckSing.play();
	}

	updateCounter = (separator = 1) => {
		this.setState((state) => (
				{
					...state,
					timer: state.timer + 5,
					points: state.points + separator
				}
			)
		)
	};

	updateErrorPoints = (separator = 1) => {
		this.setState((state) => (
				{
					...state,
					errorPoints: state.errorPoints + separator
				}
			)
		)
	};

	onSwipe = e => {
		const [right, left] = this.state.cards[0].answer;

		if ((left.version_is_true && e === 'left') || (right.version_is_true && e === 'right')) {
			this.updateCounter();
			this.state.successSing.play();
		} else {
			this.updateErrorPoints();
			this.state.errorSing.play();
		}
	};

	onAfterSwipe = () => {
		this.setState((state) => (
			{
				...state,
				cards: state.cards.slice(1, state.cards.length)
			}
		));
	};

	timerAction = () => {
		const tik = setInterval(() => {
			if (this.state.timer <= 0 || this.state.cards.length === 0) {
				clearInterval(tik);

				if(window.parent.gameData){
					window.parent.gameData.points = this.state.points;
					window.parent.gameData.totalPoints = this.state.totalPoints;
					window.parent.gameData.modal.close();
				}
			}
			if (this.state.timer === 8) {
				this.state.overTimeSing.play();
			}
			this.setState(state => (
				{
					...state,
					timer: state.timer - 1
				}
			))
		}, 1000)
	};

	updateLoader = () => {
		this.setState(
			{
				...this.state,
				loading: true
			}
		);
	};

	muteHandler=()=>{
		Howler.mute(!this.state.muted);

		this.setState(state=>({
			...state,
			muted: !state.muted
		}))
	};


	render() {
		const {timer, points, cards, loading, errorPoints} = this.state;

		if (loading) {
			return (<WasPreloader/>);
		}

		return (
			<div className='swiper-wrapper'>
				<Counter timer={timer} points={points} muted={this.state.muted} muteHandler={this.muteHandler} errorPoints={errorPoints}/>
				<Swiper
					onSwipe={this.onSwipe}
					onAfterSwipe={this.onAfterSwipe}
					cards={cards}
					helper={this.state.helper}
					updateLoader={this.updateLoader}/>
			</div>
		)
	}
};
