import {load} from './helpers/ajax-helper';

const programm = process.env.NODE_ENV;

class SwiperService {
	constructor() {
		this.mockData = {
			project: 'was',
			timer: 60,
			cards: [
				{
					text: 'First',
					answer: true,
					imageUrl: 'https://cnet1.cbsistatic.com/img/ZUz5Xm3KL07RG9tMjEZ2R-hGq4c=/1092x0/2019/04/03/378038e5-8485-43ee-8d1c-a62cab7b0d2d/lg-g8-thinq-2.jpg'
				},
				{
					text: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ab alias amet aspernatur',
					answer: false,
					imageUrl: 'https://icdn8.digitaltrends.com/image/digitaltrends/lg-v50-thinq-5g-review-04239-720x720.jpg'
				},
				{
					text: 'Third',
					answer: true,
					imageUrl: 'https://www.meme-arsenal.com/memes/de142ca54112d6c1d5b101e3901306bf.jpg'
				}
			]
		};

		this.data = null;
	}

	getData = async () => {
		if (programm === "development") {
			return await this._mockData();
		}

		const data = await this._fetchData();

		if (data) {
			let cards = [];

			data.data.questions.forEach(item => {
				cards.push({
					text: item.item_question_title,
					answer: item.items_versions,
					imageUrl: item.item_question_media.item_question_gallery[0]
						? item.item_question_media.item_question_gallery[0].url
						: null
				});
			});

			cards.sort(() => Math.random() - 0.5);

			if (data.success) {
				return {
					timer: data.data.game_custom_options && data.data.game_custom_options[0].timer,
					helper: data.data.game_custom_options && data.data.game_custom_options[1].helper,
					cards
				}
			}
		}
	};

	_mockData = ()=>{
		return new Promise((resolve, reject)=>{
			setTimeout(() => {
				resolve(this.mockData);
			}, 1000)
		})
	};

	_fetchData = () => {

		return load('action=game_items', {
			body: {
				postId: window.parent.gameData ? window.parent.gameData.postId : 56753
			}
		})
			.then(data => data.json())
	}
}

export default new SwiperService();

