import React from 'react';
import CounterTooltip from "../counter-tooltip";

const Counter = ({timer, points, muted, muteHandler, errorPoints}) => {
	return (
		<section className='counter'>
			<p className='title'>время</p>
			<div className='counter__item'>
				<button className='mute-button' onClick={muteHandler}>
					{
						muted
							? (
								<svg>
									<use href='#icon-muted'/>
								</svg>

							) : (
								<svg>
									<use href='#icon-mute'/>
								</svg>
							)
					}

				</button>
				<CounterTooltip points={points} errorPoints={errorPoints}/>
				<CounterTooltip points={errorPoints}>
					<svg>
						<use href='#icon-sad'/>
					</svg>
				</CounterTooltip>
				<div className="duck-wrapper">
					<svg>
						<use href='#icon-duck'/>
					</svg>
				</div>
				<p className='number'>{timer}</p>
			</div>

		</section>
	)
};

export default Counter;
