import React, {useEffect} from 'react';

const SwiperButtons = ({right, left, card, helper}) => {
	const keyPressHandler = ({code}) => {
		switch (code) {
			case 'ArrowRight':
				right();
				break;
			case 'ArrowLeft':
				left();
				break;
			default:
				break;
		}
	};

	useEffect(() => {
		document.addEventListener("keydown", keyPressHandler);

		return () => {
			document.removeEventListener("keydown", keyPressHandler);
		}
	});

	// todo 38 минут

	return (
		<>
			<div className="wrapper-styles question-helper">
				{
					helper?(
						<p>{helper}</p>
					):(
						<p>38 минут</p>
					)
				}
			</div>

			<div className='buttons-wrapper'>
				{
					!card.trueButton?(
						<>
							<button className='btn-custom' onClick={left}>
								{card.falseButton || card.answer[1].version_text }
							</button>

							<button className='btn-custom' onClick={right}>
								{card.trueButton || card.answer[0].version_text}
							</button>
						</>
					):(
						<>
							<button className='btn btn-red' onClick={left}>
								<svg>
									<use href='#icon-close'/>
								</svg>
							</button>

							<button className='btn btn-green' onClick={right}>
								<svg>
									<use href='#icon-approve'/>
								</svg>
							</button>
						</>
					)
				}
			</div>
		</>
	)
};

export default SwiperButtons;
