import React, {useState, useEffect} from 'react';

const CounterTooltip = ({points, children='+5'}) => {
	const [active, setCount] = useState(false);

	useEffect(()=>{
		setCount(false);

		const tik = setTimeout(()=>{
			setCount(true);
		},400);

		return ()=>{
			clearTimeout(tik)
		};

	}, [points]);


	if(points > 0){
		return (
			<div className={`counter-tooltip ${active ? 'active' : ''}`}>
				{children}
			</div>
		)
	}

	return null;
};

export default CounterTooltip;
